.burger {
  display: none;
  position: relative;
  width: 44px;
  height: 44px;
}

.burger__line,
.burger:before,
.burger:after {
  content: "";
  background-color: #fff;
  position: absolute;
  width: 100%;
  height: 3px;
  width: 30px;
  left: 7px;
  transition: transform 0.4s;
}

.burger:before {
  top: 12px;
}

.burger:after {
  bottom: 10px;
}

.burger_active::before {
  background-color: #fff;
  transform: rotate(45deg);
  top: 20px;
}

.burger_active::after {
  background-color: #fff;
  transform: rotate(-45deg);
  bottom: 21px;
}

.burger_active .burger__line {
  background-color: #fff;
  transform: scale(0);
}

.header__menu-burger {
  position: absolute;
  right: 24px;
  top: 24px;
}

@media screen and (max-width: 768px) {
  .burger {
    display: block;
  }
}

@media screen and (max-width: 480px) {
  .header__menu-burger {
    position: absolute;
    right: 13px;
    top: 13px;
  }
}
