.header__movie-link {
  font-size: 13px;
  font-weight: 400;
}

.header__movie-link.active::after {
  display: none;
}

.header__movie-link.active {
  font-weight: 500;
}

.header__movie-link:not(:last-child) {
  margin-right: 16px;
}

@media screen and (max-width: 768px) {
  .header__movie-links {
    display: none;
  }
}
